@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&family=Wallpoet&display=swap);
.lightTheme{color:black}.darkTheme{color:white}#root{overflow:auto;height:100vh;font-family:"Poppins", sans-serif;margin:0;padding:0;line-height:1;transition:all .5s ease-out}#root.lightTheme{background-image:url(/debtcounter/static/media/concrete-wall.0277c785.png),linear-gradient(rgba(255,255,255,0.0588235), rgba(0,0,0,0.0588235))}#root.darkTheme{background-color:rgba(5,16,63,0.9);background-image:url(/debtcounter/static/media/concrete-wall.0277c785.png)}.clickable{cursor:pointer}.table th,.table td,.table thead th{vertical-align:middle;border-top:none;border-bottom:none;padding-left:0em;margin:0;margin:0.25em}#totalDebt{display:none}.errorMessage{position:relative;right:0;font-size:0.75rem;transform:translateY(-1.5rem);padding:0;margin:0;display:none}#logo{z-index:10;height:100px}#debtTable input,#addForm input{border:none !important;transition:all .3s ease-out}#debtTable input.lightTheme,#addForm input.lightTheme{background-color:rgba(0,0,0,0.1) !important}#debtTable input.lightTheme:hover,#addForm input.lightTheme:hover{background-color:rgba(0,0,0,0.3) !important}#debtTable input.darkTheme,#addForm input.darkTheme{background-color:rgba(255,255,255,0.1) !important}#debtTable input.darkTheme:hover,#addForm input.darkTheme:hover{background-color:rgba(0,0,0,0.3) !important}#debtTable input.lightTheme,#addForm input.lightTheme{color:black}#debtTable input.darkTheme,#addForm input.darkTheme{color:white}#debtTable,#addForm,#localeForm,#debtConversiontable{width:100%;margin-bottom:2rem;z-index:10}.medium-icon{font-size:1.5rem}@media (min-width: 800px){body{overflow:hidden}#totalDebt{font-size:10rem;font-family:"Wallpoet", cursive;font-weight:bold;display:block;position:fixed;width:1920;overflow:hidden;bottom:1rem}#debtTable{position:absolute;top:4rem;right:1rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;max-height:50vh;overflow:auto}#debtTable .table{text-align:center}#debtTable::-webkit-scrollbar{width:0.15rem}#debtTable::-webkit-scrollbar-track{background:transparent}#debtTable::-webkit-scrollbar-thumb{background:#86050575}#localeForm,#addForm{width:300px}#debtConversionTable{position:absolute;bottom:0;right:0;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}}

